import React, { useState } from "react";
import TermAndCondition from "../../../components/TermAndCondition";
import { Modal } from "react-responsive-modal";
import { dateFormat } from "../utils";
import Map from "../components/Map";

const styles = {
  button: {
    width: "100%",
    backgroundColor: "#7D7BE6",
    color: "white",
    borderRadius: 12,
    padding: "20px 15px",
    display: "block",
    textAlign: "center",
    textTransform: "uppercase",
    fontSize: 20,
    lineHeight: "24px",
    fontWeight: 300,
    border: 0,
    boxShadow: "none",
    marginBottom: 10,
  },
  fullWidth: {
    flex: "0 0 100%",
  },
  domEl: {
    display: "none",
    backgroundColor: "#efefef",
    width: "100%",
    position: "relative",
  },
};

function Save(props) {
  const [open, setOpen] = useState(false);
  const onOpenModal = () => setOpen(true);

  return (
    <div style={styles.fullWidth}>
      <TermAndCondition Href={props.termsConditionHref} />
      <button
        type="button"
        onClick={onOpenModal}
        style={styles.button}
        ref={props.save}
      >
        Save
      </button>
      <Modal open={open} onClose={() => setOpen(false)} center>
        <div>
          <h2>Save as:</h2>
          <p>Downloads/{`${props.downloadUrl}.jpeg`}</p>
          <div style={{ textAlign: "center" }}>
            <button onClick={props.handleSubmit} ref={props.saveRef}>
              OK
            </button>
          </div>
        </div>
      </Modal>
      <div id="domEl" ref={props.imgRef} style={styles.domEl}>
        <div>
          <img src={props.state.image} style={{ maxWidth: "100%" }} />
          <div className="img-coords">
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <div style={{ flex: "0 0 35%" }}>
                <Map state={props.state} />
              </div>
              <div style={{ flex: "0 0 62%" }}>
                {props.state.editAddr ? (
                  <p
                    dangerouslySetInnerHTML={{
                      __html: props.highlightNewWords(
                        props.state.currentAddr,
                        props.state.addr
                      ),
                    }}
                    style={{ textAlign: "center", fontSize: 10 }}
                  ></p>
                ) : (
                  <p style={{ textAlign: "center", fontSize: 10 }}>
                    {props.state.addr}
                  </p>
                )}
                <table className="table">
                  <thead>
                    <tr>
                      <th></th>
                      <th>Decimal</th>
                      <th>DMS</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>
                        <b>Latitude</b>
                      </td>
                      {props.state.editGeo ? (
                        <td
                          dangerouslySetInnerHTML={{
                            __html: `${props.highlightNewWords(
                              String(props.location.state.item.lat),
                              String(props.state.lat)
                            )}`,
                          }}
                        ></td>
                      ) : (
                        <td>{props.state.lat}</td>
                      )}
                      <td>
                        {props.convertLatLngToDMS(
                          props.state.lat,
                          props.state.lon,
                          "lat"
                        )}
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <b>Longitude</b>
                      </td>
                      {props.state.editGeo ? (
                        <td
                          dangerouslySetInnerHTML={{
                            __html: `${props.highlightNewWords(
                              String(props.location.state.item.lon),
                              String(props.state.lon)
                            )}`,
                          }}
                        ></td>
                      ) : (
                        <td>{props.state.lon}</td>
                      )}
                      <td>
                        {props.convertLatLngToDMS(
                          props.state.lat,
                          props.state.lon,
                          "lon"
                        )}
                      </td>
                    </tr>
                  </tbody>
                </table>

                <p>{dateFormat(new Date())}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Save;
