import React, { useReducer, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useCurrentPosition } from "react-use-geolocation";
const FetchingLocation = () => {
    let location = useLocation();
    const navigate = useNavigate();
    const [position, error] = useCurrentPosition();
    if (!position && !error) {
      return <p className="position-msg">Waiting...</p>;
    }
  
    if (error) {
      return (
        <p className="position-msg">
          {error.message === "User denied Geolocation"
            ? "Location access is disabled. Please enable to proceed."
            : error.message}
        </p>
      );
    }
    if(position) {
        navigate(`/get-started`, {
            state: {
              item: {
                name: location.state.item.name,
                source: location.state.item.source,
                lat: position.coords.latitude,
                lon: position.coords.longitude
              },
            },
          });
    }
    return (
        <div>
            &nbsp;
        </div>
    );
};

export default FetchingLocation;