import React from "react";
import GoogleMapReact from "google-map-react";

const Map = ({ state }) => {
  const latitude = state.lat;
  const longitude = state.lon;
  const apiKey = "AIzaSyAhXrbXpk2xom4AqMI9rghnxb1I2rjvLWo";
  const markerPosition = `${latitude},${longitude}`;
  const imageUrl = `https://maps.googleapis.com/maps/api/staticmap?center=${markerPosition}&zoom=12&size=200x200&markers=${markerPosition}&key=${apiKey}`;
  return (
    <div
      style={{
        height: "125px",
        width: "100%",
        position: "relative",
        display: "inline-block",
        overflow: "hidden",
      }}
    >
      <img
        src={imageUrl}
        alt="Map Image"
        style={{
          position: "absolute",
          width: "100%",
          top: "50%",
          left: "50%",
          transform: "translate(-50%,-50%)",
          height: "100%",
          objectFit: "cover",
        }}
      />
    </div>
  );
};

export default Map;
